import { Component, OnInit } from '@angular/core';
import { AuthService } from '../auth.service';
import { Router }  from '@angular/router';


@Component({
  selector: 'app-registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.sass']
})
export class RegistrationComponent implements OnInit {

  name: String;
  login: String;
  email: String;
  password_1: String;
  password_2: String;

  nameSet: Boolean;
  loginSet: Boolean;
  emailSet: Boolean;
  password_1Set: Boolean;
  password_2Set: Boolean;

  password: String;

  dangerOpen: Boolean = false;
  goodItem: String;
  item: String;

  constructor(
    private router: Router,
    private authService: AuthService,
  ) { }

  ngOnInit(): void {
  }

   submitRegisterForm() {
     const user = {
       name: null,
       login: null,
       email: null,
       password: null
     };

    if (this.name == undefined || this.name == '') {
      this.nameSet = false;
      this.item = 'ім\'я';
      this.dangerOpen = true;
      user.name = null;
      return false
    } else {
      this.nameSet = true;
      user.name = this.name
    };


     if (this.login == undefined || this.login == '') {
       this.loginSet = false;
       this.item = 'логін';
       this.dangerOpen = true;
       user.login = null;
       return false
     } else {
       this.loginSet = true;
       user.login = this.login
     };


     if (this.email == undefined || this.email == '') {
       this.emailSet = false;
       this.item = 'електронна адреса';
       this.dangerOpen = true;
       user.email = null;
       return false
     } else {
       this.emailSet = true;
       user.email = this.email
     };




     if (this.password_1 == undefined || this.password_1 == '') {
       this.password_1Set = false;
       this.item = 'пароль';
       user.password = null;
       this.dangerOpen = true;
       return false
     } else {
       this.password_1Set = true;
     };

     if (this.password_2 == undefined || this.password_2 == '') {
       this.password_2Set = false;
       this.item = 'підтвердіть пароль';
       user.password = null;
       this.dangerOpen = true;
       return false
     } else {
       this.password_2Set = true;
     };

     if (this.password_1 !== this.password_2) {
       user.password = null;
       this.item = 'паролі не співпадають'
       this.dangerOpen = true;
       console.log('invalid PASS')
       return false
     } else  {
       console.log('compare pass :',this.password_1 === this.password_2);
       user.password = this.password_1;
       this.item = null;
       this.dangerOpen = false;
       // this.goodItem = 'Реєстрація успішна'
       setTimeout ( () => this.goodItem = undefined , 3000);
     };
     console.log(user)


    this.authService.registerUser(user).subscribe(data => {
      console.log('data', data)
        // @ts-ignore
      if(!data.success) {
          // @ts-ignore
          console.log(data.message)
          // @ts-ignore
          this.item = data.message;
          this.dangerOpen = true;
          return this.router.navigate(['/registration'])
        } else {
          // @ts-ignore
          console.log(data.message)
          this.goodItem = 'Реєстрація успішна'
          return this.router.navigate(['/authentication'])
        }
    });

  }
}
