import { BrowserModule } from '@angular/platform-browser';
import { LOCALE_ID, NgModule } from '@angular/core';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { RouterModule, Routes  } from '@angular/router';
import { RegistrationComponent } from './registration/registration.component';
import { AuthenticationComponent } from './authentication/authentication.component';
import { UserPageComponentComponent } from './user-page-component/user-page-component.component';
import { StatisticPageComponent } from './statistic-page/statistic-page.component';
import { HomePageComponent } from './home-page/home-page.component';
import { FlashMessagesModule }  from 'angular2-flash-messages';
import { AuthService }  from './auth.service';
import { InspectionService }  from './inspection.service';
import { IsLoggedIn }  from './isLogged.guard';
import { FormsModule } from "@angular/forms";
import { CheckFormService } from "./check-form.service";
import { HttpClientModule } from '@angular/common/http';
import { SomeDataService } from './some-data.service';
import { GoogleChartsModule } from 'angular-google-charts';
import '@angular/common/locales/global/uk';

const appRoute: Routes = [
  { path: '', component: HomePageComponent},
  { path: 'authentication', component: AuthenticationComponent},
  { path: 'registration', component: RegistrationComponent},
  { path: 'userPage', component: UserPageComponentComponent, canActivate: [IsLoggedIn]},
  { path: 'statistic', component: StatisticPageComponent, canActivate: [IsLoggedIn]},
]

// @ts-ignore
@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    UserPageComponentComponent,
    FooterComponent,
    RegistrationComponent,
    AuthenticationComponent,
    HomePageComponent,
    StatisticPageComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    RouterModule.forRoot(appRoute),
    FormsModule,
    FlashMessagesModule.forRoot(),
    HttpClientModule,
    GoogleChartsModule
  ],
  providers: [
    CheckFormService,
    AuthService,
    InspectionService,
    SomeDataService,
    { provide: LOCALE_ID, useValue: 'uk' },
    IsLoggedIn
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
