 import { Component, OnInit } from '@angular/core';
 import { AuthService } from '../auth.service';
 import { FlashMessagesService }  from 'angular2-flash-messages';
 import { Router }  from '@angular/router';
 import {SomeDataService} from "../some-data.service";

@Component({
  selector: 'app-authentication',
  templateUrl: './authentication.component.html',
  styleUrls: ['./authentication.component.sass']
})
export class AuthenticationComponent implements OnInit {

  login: String;
  password: String;

  loginSet: Boolean;
  passwordSet: Boolean;

  dangerOpen: Boolean = false;
  goodItem: String;
  item: String;
  danger: String;

  constructor(
    private router: Router,
    private authService: AuthService
  ) { }

  ngOnInit(): void {

  }


  userLogin() {
    const user = {
      login: null,
      password: null
    };


    if (this.login == undefined || this.login == '') {
      this.loginSet = false;
      this.item = 'логін';
      this.dangerOpen = true;
      user.login = null;
      return false
    } else {
      this.loginSet = true;
      user.login = this.login
    };
    console.log(user)
    console.log(this.item)



    if (this.password == undefined || this.password == '') {
      this.passwordSet = false;
      this.item = 'пароль';
      user.password = null;
      this.dangerOpen = true;
      return false
    } else {
      this.passwordSet = true;
      user.password = this.password;
      this.item = null;
      this.dangerOpen = false;
      setTimeout ( () => this.goodItem = undefined , 3000);
    };
    console.log(user)
    console.log(this.item)


    console.log(user)
    console.log(this.item)





    this.authService.authUser(user).subscribe(data => {
      // @ts-ignore
      if(!data.success) {
        // @ts-ignore
        console.log('if !data.success.message', data.message)
        // @ts-ignore
        this.danger = data.message;
        return false
      } else {
        // @ts-ignore
        this.authService.storeUser(data.token, data.user);
        // @ts-ignore
        this.goodItem = data.message
        console.log('main auth data', data)
        this.goodItem = 'Автентифікація успішна'
        return this.router.navigate(['/userPage']);
      }
    });

  }

}
